:root {
  --Cgreen: #79c142;
  --Cgrey: #5c5c5e;
  --CWgreen:#a8eb76;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,body, #root {
  // position: fixed;
  top: 0;
  left: 0;
  font-family: sans-serif;
  text-align: center;
  height: 100%;
  width: 100%;
  justify-content: center;
  align-items: center;
}

.canvas{
  position: fixed;
  top: 0;
  left: 0;
  font-family: sans-serif;
  text-align: center;
  height: 100%;
  width: 100%;
  background:rgb(255, 255, 255);
  display: flex;
  justify-content: center;
  align-items: center;
  z-Index: -1;
}


.switch-wrapper {
  position: relative;
}

.switch-wrapper > div {
  height: 100%;
}